// =============================================================================
// Handover Tags
// =============================================================================

export const HANDOVER_TAGS = [
  { tag: '@HANDOVER', description: '', format: '// @HANDOVER: <>' },
  { tag: '@WIP', description: '', format: '// @WIP: <>' },
  { tag: '@BLOCKED', description: '', format: '// @BLOCKED: <>' },
  { tag: '@DECISION', description: '', format: '// @DECISION: <>' },
  { tag: '@REVIEW', description: '', format: '// @REVIEW: <>' },
  { tag: '@FIXME', description: '', format: '// @FIXME: <>' },
  { tag: '@HACK', description: '', format: '// @HACK: <>' },
];

export function extractHandoverTags(source: string): { tag: string; line: number; content: string }[] {
  const results: { tag: string; line: number; content: string }[] = [];
  const lines = source.split('\n');
  const tagPattern = /@(HANDOVER|WIP|BLOCKED|DECISION|REVIEW|FIXME|HACK)\s*:\s*(.+)/gi;
  
  for (let i = 0; i < lines.length; i++) {
    const line = lines[i]!;
    tagPattern.lastIndex = 0;
    const match = tagPattern.exec(line);
    if (match) {
      results.push({
        tag: match[1]!.toUpperCase(),
        line: i + 1,
        content: match[2]!.trim(),
      });
    }
  }
  
  return results;
}
